import React, { Component } from "react";
import { debounce } from "lodash";
import { SearchBox } from "./components/search-box/search-box.component";
import { ItemList } from "./components/item-list/item-list.component";
import { Item } from "./components/item/item.component";
import { Popup } from "./components/popup/popup.component";
import { Dnd } from "./components/dnd/dnd.component";
import dragBtn from "./icons/drag_icon.png";
import "./App.css";

class App extends Component {
  constructor() {
    super();
    this.state = {
      photos: [],
      searchField: "",
      apiUrl: "https://pixabay.com/api",
      apiKey: "22698874-5e3ff1ef81847d81571af2bbf",
      rotation: 0,
      showModal: false,
      rotId: 0,
      modalId: 0,
      draggedImg: undefined,
      dragIndexX: 0,
      dragIndexY: 0,
      dragPos: {
        x: [0, 0],
        y: [0, 0],
      },
      draggable: false,
      imgWidth: 0,
      imgHeight: 0,
    };
  }
  debounceEvent(...args) {
    this.debouncedEvent = debounce(...args);
    return (e) => {
      e.persist();
      return this.debouncedEvent(e);
    };
  }
  componentWillUmaount() {
    this.debouncedEvent.cancle();
  }

  handleChange = (event) => {
    let val = event.target.value;
    this.setState({ searchField: val });
    if (val === "") {
      this.setState({ photos: [] });
    } else {
      console.log(event.target.value);
      fetch(
        `${this.state.apiUrl}/?key=${this.state.apiKey}&q=${val}&image_type=photo&per_page=20`
      )
        .then((res) => res.json())
        .then((data) => this.setState({ photos: data.hits }))
        .catch((err) => console.error(err));
    }
  };

  handleRotateClick = (photo) => {
    let newRotation = this.state.rotation - 90;
    if (newRotation === -360) newRotation = 0;

    let updateRotId = photo.id;
    this.setState({
      rotation: newRotation,
      rotId: updateRotId,
    });
  };

  handleExpandClick = (photo) => {
    let updateModel = !this.state.showModal;
    let updateId = photo.id;
    this.setState({ showModal: updateModel, modalId: updateId });
  };

  handleDeleteClick = (index) => {
    let arr = this.state.photos;
    arr.splice(index, 1);
    this.setState({ photos: arr });
  };
  //////
  handleOnDrag = (photo, index) => {
    this.setState({ draggedImg: photo, dragIndexX: index });
  };

  handleOnDrop = (e, photo, index) => {
    const arr = this.state.photos;
    console.log();
    if (e.type === "touchend") {
      let draggedIndex = this.state.dragIndexX;
      const arr = this.state.photos;
      console.log(arr);
      this.setState({ photos: arr }, () => {
        arr.splice(index, 1);
        arr.splice(draggedIndex, 0, photo);
      });
    } else {
      console.log(index);

      let draggedPhoto = this.state.draggedImg;

      let draggedIndex = this.state.dragIndexX;

      arr.splice(draggedIndex, 1);
      arr.splice(index, 0, draggedPhoto);
    }

    this.setState({ photos: arr });
  };

  handleOnDragOver = (e) => {
    e.preventDefault();
  };
  ///////////////////////////////////////////
  onTouchStart = (e, index) => {
    console.log(
      "onTouchS: ",
      "(",
      e.changedTouches[0].clientX,
      ", ",
      e.changedTouches[0].clientY,
      ")"
    );
    console.log(e.target.offsetParent);
    // this.setState({ draggable: true });
    let imgWidth = e.target.getBoundingClientRect().width;
    let imgHeight = e.target.getBoundingClientRect().height;
    let left = e.target.getBoundingClientRect().x + imgWidth;
    let up = e.target.getBoundingClientRect().y + imgHeight;
    let right = e.target.getBoundingClientRect().x - 150;
    let down = e.target.getBoundingClientRect().y - 150;
    // let right = e.target.getBoundingClientRect().x - 150;
    // let down = e.target.getBoundingClientRect().y - 150;

    console.log(e.target.getBoundingClientRect());
    console.log(`(${left}, ${up})`);
    // console.log(left, ", ", up);
    this.setState(() => {
      return {
        dragIndexX: index,
        dragPos: {
          x: [left, right],
          y: [up, down],
        },
        imgWidth: imgWidth,
        imgHeight: imgHeight,
      };
    });
  };

  onTouchMove = (e) => {
    e.target.offsetParent.style.position = "fixed";
    const imgWidth = this.state.imgWidth;
    const imgHeight = this.state.imgHeight;
    const imgWidthCenter = imgWidth / 2;
    const imgHeightCenter = imgHeight / 2;
    let img = e.target.offsetParent;
    let posX = e.changedTouches[0].clientX - imgWidthCenter;
    let posY = e.changedTouches[0].clientY - imgHeightCenter;
    this.setState((prevState) => {
      let left = prevState.dragPos.x[0];
      let up = prevState.dragPos.y[0];
      let right = prevState.dragPos.x[1];
      let down = prevState.dragPos.y[1];

      // let draggedPhoto = this.state.draggedImg;
      // console.log(`(${posX}, ${posY})`);
      // console.log(`(${left}, ${up})`);

      let draggedIndex = prevState.dragIndexX;
      console.log(draggedIndex);
      let pageWidth = Math.floor(window.innerWidth / imgWidth);
      console.log(pageWidth, "..", "podsX = ", posX, ">=", "left = ", left);
      // console.log(pageWidth, "..", "podsY = ", posY, ">=", "up = ", up);
      // console.log(pageWidth, "..", "podsX = ", posX, "<=", "right = ", right);
      // console.log(pageWidth, "..", "podsY = ", posY, "<=", "down = ", down);
      if (posX >= left) {
        // if (draggedIndex + pageWidth <= this.state.photos.length) {
        draggedIndex += 1;

        right = posX - imgWidthCenter;
        left += imgWidth;
        // }
      }
      if (posY >= up) {
        if (draggedIndex + pageWidth <= this.state.photos.length) {
          draggedIndex = draggedIndex + pageWidth;

          down = posY - imgHeightCenter;
          up += imgHeight;
          left = posX;
        }
      }
      if (posX <= right && draggedIndex >= 0) {
        draggedIndex -= 1;

        right -= imgWidth;
      }
      if (posY <= down && draggedIndex >= 0) {
        draggedIndex = draggedIndex - pageWidth;

        down -= imgHeight;
      }

      return {
        dragIndexX: draggedIndex,
        dragPos: {
          x: [left, right],
          y: [up, down],
        },
      };
    });

    img.style.left = posX + "px";
    img.style.top = posY + "px";
    img.style.zIndex = 15;
  };
  onTouchEnd = (e, photo, index) => {
    console.log(
      "onTouchS: ",
      "(",
      e.changedTouches[0].clientX,
      ", ",
      e.changedTouches[0].clientY,
      ")"
    );
    // this.setState((prevState) => {
    let draggedIndex = this.state.dragIndexX;
    const arr = this.state.photos;

    arr.splice(index, 1);
    arr.splice(draggedIndex, 0, photo);
    // console.log(draggedIndex);
    // });
    this.setState({ photos: arr, draggable: false });
  };

  toggleDraggable = () => {
    this.setState((prevState) => {
      if (prevState.draggable) return { draggable: !prevState.draggable };

      return { draggable: !prevState.draggable };
    });
  };

  render() {
    const images = this.state.photos.map((photo, index) => {
      let newStyle;

      if (photo.id === this.state.rotId) {
        newStyle = { transform: `rotate(${this.state.rotation}deg)` };
      }
      if (this.state.draggable) {
        return (
          <Dnd
            key={photo.id}
            id={photo.id}
            index={index}
            photo={photo}
            rotateStyle={newStyle}
            handleExpandClick={() => this.handleExpandClick(photo)}
            handleRotateClick={() => this.handleRotateClick(photo)}
            handleDeleteClick={() => this.handleDeleteClick(index)}
            handleOnDrag={() => this.handleOnDrag(photo, index)}
            handleOnDrop={(e) => this.handleOnDrop(e, photo, index)}
            handleOnDragOver={this.handleOnDragOver}
            onTouchStart={(e) => this.onTouchStart(e, index)}
            onTouchEnd={(e) => this.onTouchEnd(e, photo, index)}
            onTouchMove={(e) => this.onTouchMove(e)}
          />
        );
      }

      if (photo.id === this.state.modalId && this.state.showModal)
        return (
          <Popup
            id={photo.id}
            index={index}
            key={photo.id}
            photo={photo}
            rotateStyle={newStyle}
            handleExpandClick={() => this.handleExpandClick(photo)}
          />
        );

      return (
        <Item
          key={photo.id}
          id={photo.id}
          index={index}
          photo={photo}
          rotateStyle={newStyle}
          handleExpandClick={() => this.handleExpandClick(photo)}
          handleRotateClick={() => this.handleRotateClick(photo)}
          handleDeleteClick={() => this.handleDeleteClick(index)}
        />
      );
    });

    return (
      <div className="App">
        <h1>Photo Gallery</h1>
        <div className="header">
          <img
            onClick={this.toggleDraggable}
            className={`drag-btn ${
              this.state.draggable ? "draggable-btn" : "not-draggable-btn"
            }`}
            src={dragBtn}
            alt="draggable button"
          />

          <SearchBox
            handleChange={this.debounceEvent(this.handleChange, 1000)}
          />
        </div>

        {this.state.searchField.length > 0 ? (
          <ItemList photos={images} />
        ) : null}
      </div>
    );
  }
}

export default App;
