import React from "react";
import rotateBtn from "../item/icons/rotate_icon.png";
import deleteBtn from "../item/icons/delete_icon.png";
import expandBtn from "../item/icons/expand_icon.png";

import "../item/item.styles.css";
import "./dnd.styles.css";
export const Dnd = (props) => (
  <div className="item">
    <img
      id={props.id}
      index={props.index}
      src={`${props.photo.largeImageURL}`}
      alt="img"
      className="image"
      style={props.rotateStyle}
      onDragStart={props.handleOnDrag}
      onDrop={props.handleOnDrop}
      onDragOver={props.handleOnDragOver}
      onTouchStart={props.onTouchStart}
      onTouchEnd={props.onTouchEnd}
      onTouchMove={props.onTouchMove}
    />
    <div className="image-footer">
      <img
        id={props.id}
        src={deleteBtn}
        alt="delete icon"
        className="delete-icon"
        onClick={props.handleDeleteClick}
      />
      <img
        id={props.id}
        src={expandBtn}
        alt="expand icon"
        className="expand-icon"
        onClick={props.handleExpandClick}
      />

      <img
        id={props.id}
        src={rotateBtn}
        alt="rotate icon"
        className="rotate-icon"
        onClick={props.handleRotateClick}
      />
    </div>
  </div>
);
